@import url(https://fonts.googleapis.com/css?family=Open+Sans:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Open Sans", sans-serif;
  background-color: rgb(238, 238, 238) !important;
}
.row {
  margin-left: 0px !important;
  margin-right: 0px !important;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #f1f3f2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
input[type="number"] {
  width: 100%;
}

.TopMenuDiv {
  overflow: hidden;
  background: rgb(74, 29, 89);
  background: linear-gradient(
    90deg,
    rgba(74, 29, 89, 1) 0%,
    rgba(106, 62, 122, 1) 35%,
    rgba(120, 89, 131, 1) 100%
  );
  position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  height: 65px;
  width: 100%; /* Full width */

  z-index: 1;
}
.Logo {
  font-family: "Open Sans", sans-serif;
  color: white;
  font-weight: bolder;
  font-size: 48px;
  float: left;
  padding-left: 5%;
}
.ButtonsDiv {
  overflow: hidden;
  height: 100%;
  float: right;
  padding-right: 5%;
}
.ButtonsDiv button {
  border-style: solid;
  border-color: white;
  border-width: 2px;
  background-color: #6a3e7a;
  color: white;
  height: 35px;
  margin-top: 15px;
  font-family: inherit;
  font-size: 14px;
  margin-right: 28px;
  padding: 0 18px 0 18px;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.4s;
}

.ButtonsDiv button:hover {
  cursor: pointer;
  background-color: #6cbc79;
}

button:focus {
  outline: 0 !important;
}

.OptionTextTop {
  background-color: #6a3e7a;
  width: 100%;
  color: rgb(255, 255, 255);
  padding-left: 5px;
  padding-right: 5px;
  margin-bottom: 5px;
  border: white solid 2px;
}




.Footer {
  font-family: "Open Sans", sans-serif;
  text-align: right;
  background-color: #e7e7e7;
  font-size: 14px;
  color: rgb(92, 92, 92);
  height: 85px;
  width: 100%;
  bottom: 0;
}
.Footer footer {
  height: 20px;
  padding-top: 30px;
  padding-right: 5%;

}

.Footer footer a {
  text-decoration: none;
  font-weight: 800;
}

.Main {
  padding-top: 95px;
  padding-bottom: 30px;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  display: inline-block;
}
.preview_text_div {
  margin: 0;
  top: 50%;
  font-size: 35px;
  letter-spacing: 4px;
  word-spacing: 3px;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  font-style: normal;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  text-transform: capitalize;
}
.preview_code {
  width: 100%;
  padding-top: 15px;
  padding-left: 30px;
  padding-right: 30px;
}
.preview_code textarea {
  resize: none;
}
.Main main {
  padding: 30px 30px 30px 30px;
  height: 100%;
}

.template {
  width: 100%;

  /*background-color: rgb(255, 255, 255);*/
  /*box-shadow: 0px 0px 85px 19px rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: 0px 0px 85px 19px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: 0px 0px 85px 19px rgba(0, 0, 0, 0.6);*/
}

.SideMenu {
  padding-top: 95px;
  padding-bottom: 30px;
  width: 100%;
  box-sizing: border-box;
  display: inline-block;
}

.SideMenu menu {
  padding: 30px 30px 30px 30px;
  background-color: #6cbc79;
  height: 66vh;
}

